* {
  padding: 0;
  margin: 0;
}
body {
  padding: 10px;
  font-family: 'Times New Roman', Times, serif;
  width: 700px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  font-size: 3rem;
}

table thead {
  font-weight: bold;
}
table,
table tr td {
  border: 1px solid black;
  border-collapse: collapse;
  padding: 5px;
}

.big {
  font-size: 30px;
}

h3,
h4 {
  margin: 10px 0;
}

.sketch {
  margin: 10px 0;
}
.center {
  text-align: center;
}

input,
button {
  outline: none;
}
.field-group {
  display: inline-flex;
  margin: 0 5px;
}
.field-group input {
  border: 1px solid gray;
  border-right: 0;
  padding: 3px;
}
.field-group input.input-small {
  width: 60px;
}
.field-group span {
  width: fit-content;
  padding: 3px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ddd;
  border: 1px solid gray;
}
.copyright {
  margin-top: 20px;
}
